/* ## Gallery
--------------------------------------------- */
.gallery,
.gallery-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.gallery a {
  display: block;
  min-height: 100%;
}

.gallery img {
  height: auto;
  width: 100%;
  display: block;
}

.gallery-caption {
  display: block;
}

.gallery-item {
