.footer-widgets .widget-area:last-of-type {
  margin-bottom: 0;
}

.footer-widgets .widget-area .widget {
  width: 100%;
  display: block;
  margin: 0 auto 2em;
}

.footer-widgets .widget-area .widget:last-of-type {
  margin-bottom: 0;
}

/* ## Simple Social Icons
--------------------------------------------- */
.simple-social-icons {
  overflow: hidden;
}

.simple-social-icons svg[class^="social-"], .simple-social-icons svg[class*=" social-"] {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.simple-social-icons ul {
  margin: -0.5em -0.5em 0 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.simple-social-icons ul li {
  background: none;
  border: none;
  list-style-type: none;
  margin: 0.5em 0.5em 0 0;
  padding: 0;
}

.simple-social-icons ul li a {
  border: none;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  height: 1em;
  line-height: 1em;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  width: 1em;
}

.simple-social-icons ul.aligncenter {
  justify-content: center;
}

.simple-social-icons ul.alignleft {
  justify-content: flex-start;
}

.simple-social-icons ul.alignright {
  justify-content: flex-end;
}

/* ## Gravity Forms
--------------------------------------------- */
.gform_heading,
.gform_body,
.gform_footer {
  clear: both;
}

.gform_heading {
  padding-bottom: 1em;
}

.gform_wrapper .gform_fields {
  list-style: none;
  margin: 0 -10px;
  margin: 0 -1rem;
  padding: 0;
}

.gform_wrapper .gform_fields li {
  list-style-type: none;
}

.gform_wrapper .gfield_checkbox,
.gform_wrapper .gfield_radio {
  list-style: none;
  padding: 0;
}

.ginput_container span {
  margin-bottom: 10px;
  margin-bottom: 1rem;
}

.ginput_container input,
.ginput_container select,
.ginput_container textarea {
  width: 100%;
}

.ginput_container input,
.ginput_container textarea {
  margin: 0;
}

.gfield_label {
  display: inline-block;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.gfield_label:empty {
  display: none;
}

.left_label .gfield_label {
  float: left;
}

.right_label .gfield_label {
  float: right;
}

.gfield_required {
  padding-left: 0.1em;
}

.gfield_checkbox li,
.gfield_radio li {
  width: 100%;
}

.gfield_checkbox input,
.gfield_radio input {
  width: auto;
  float: left;
  margin-right: 10px;
  margin-right: 1rem;
}

.gfield_checkbox label,
.gfield_radio label {
  float: left;
  line-height: 1;
}

.gsection {
  margin: 10px 0;
  margin: 1rem 0;
}

.gsection h2 {
  border-bottom: 1px solid #f0f1f2;
  border-bottom: 0.1rem solid #f0f1f2;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.validation_error,
.gf_progressbar_wrapper {
  padding-bottom: 1em;
}

.gf_progressbar {
  background: #f4f5f6;
}

.gf_progressbar_percentage {
  background: #30353a;
  padding: 5px 10px;
  padding: 0.5rem 1rem;
  text-align: right;
}

.gfield_time_hour,
.gfield_time_minute,
.gfield_time_ampm {
  display: inline-block;
}

.gfield_time_hour label,
.gfield_time_minute label {
  display: block;
}

.gfield_time_ampm {
  vertical-align: top;
}

.gfield {
  width: 100%;
  list-style-type: none;
  clear: both;
  overflow: auto;
  padding: 0 10px 15px;
  padding: 0 1rem 1.5rem;
  margin: 0;
}

.gfield .validation_message {
  margin-top: 10px;
  margin-top: 1rem;
}

.gfield li {
  list-style-type: none;
}

.gform_validation_container {
  display: none;
}

.gform_footer .gform_button {
  width: auto;
}

.ginput_full input,
.ginput_full select,
.ginput_full textarea,
.ginput_left input,
.ginput_left select,
.ginput_left textarea,
.ginput_right input,
.ginput_right select,
.ginput_right textarea {
  margin-bottom: 5px;
  margin-bottom: 0.5rem;
  width: 100%;
}

.ginput_full label,
.ginput_left label,
.ginput_right label {
  display: block;
}

.ginput_full {
  display: block;
}

.ginput_left {
  float: left;
  padding-right: 10px;
  padding-right: 1rem;
}

.ginput_right {
  float: right;
}

.gf_left_half,
.gf_left_third,
.gf_middle_third,
.gf_first_quarter,
.gf_second_quarter {
  clear: none;
  float: left;
  padding-right: 10px;
  padding-right: 1rem;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.gf_right_half,
.gf_right_third,
.gf_fourth_quarter {
  clear: none;
  float: right;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.gf_third_quarter {
  clear: none;
  float: left;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.gf_list_2col li,
.gf_list_3col li,
.gf_list_4col li,
.gf_list_5col li {
  float: left;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

.gf_list_inline li {
  display: inline-block;
  padding-right: 10px;
  padding-right: 1rem;
  padding-bottom: 10px;
  padding-bottom: 1rem;
}

@media (min-width: 512px) {
  .ginput_container input.large,
  .ginput_container select.large,
  .ginput_container textarea.large {
    width: 100%;
  }
  .ginput_container input.medium,
  .ginput_container select.medium,
  .ginput_container textarea.medium {
    width: 66%;
  }
  .ginput_container input.small,
  .ginput_container select.small,
  .ginput_container textarea.small {
    width: 33%;
  }
  .ginput_left,
  .ginput_right {
    width: 50%;
  }
  .left_label label.gfield_label,
  .right_label label.gfield_label {
    width: 25%;
  }
  li.gf_left_half, li.gf_right_half,
  .gf_list_2col li {
    width: 50%;
  }
  li.gf_left_third, li.gf_right_third,
  .gf_list_3col li {
    width: 33%;
  }
  li.gf_middle_third {
    width: 34%;
  }
  li.gf_first_quarter, li.gf_second_quarter, li.gf_third_quarter, li.gf_fourth_quarter,
  .gf_list_4col li {
    width: 25%;
  }
  .gf_list_5col li {
    width: 20%;
  }
}

/* ## Front Page
--------------------------------------------- */
.front-page-1,
.front-page-2,
.front-page-3,
.front-page-4,
.front-page-5 {
  width: 100%;
  padding: 60px 0;
  padding: 6rem 0;
  background-color: #ffffff;
  border-bottom: 1px solid #f0f1f2;
  border-bottom: 0.1rem solid #f0f1f2;
}

@media (min-width: 768px) {
  .front-page-1,
  .front-page-2,
  .front-page-3,
  .front-page-4,
  .front-page-5 {
    padding: 6rem 0 2rem;
  }
}

@media (min-width: 768px) {
  .front-page-1 .wrap,
  .front-page-2 .wrap,
  .front-page-3 .wrap,
  .front-page-4 .wrap,
  .front-page-5 .wrap {
    display: flex;
    flex-wrap: wrap;
  }
}

.front-page-1 {
  padding: calc(20vw + 7rem) 0 20vw;
  position: relative;
  background-color: #30353a;
  clear: both;
}

.front-page-1:before, .front-page-1:after {
  clear: both;
  display: block;
  content: '';
}

@media (min-width: 768px) {
  .front-page-1 {
    padding: calc(20vh + 12rem) 0 20vh;
  }
}

.front-page-1 .wrap {
  position: relative;
  text-align: center;
}

@media (min-width: 896px) {
  .front-page-1 .wrap {
    max-width: 768px;
  }
}

.front-page-1 .widget {
  background-color: transparent;
}

.front-page-1 .widget:last-of-type {
  margin-bottom: 10px;
  margin-bottom: 1rem;
}

.front-page-1 h1 {
  margin: 0 0 0.618em;
}

.front-page-1 p {
  margin: 0 0 1.382em;
}

/* ## Custom Header
--------------------------------------------- */
.wp-custom-header {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

.wp-custom-header img,
.wp-custom-header video,
.wp-custom-header iframe {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: auto;
  height: auto;
  min-height: 100%;
  min-width: 100%;
  max-width: 100%;
  max-height: none;
  object-fit: cover;
}

.wp-custom-header iframe {
  left: -25%;
  right: -25%;
  height: 150%;
  width: 150%;
}

/* # Print Styles
--------------------------------------------- */
@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    box-shadow: none !important;
    color: #30353a !important;
    text-shadow: none !important;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  a[href^="javascript:"]:after,
  a[href^="#"]:after,
  .site-title > a:after {
    content: "";
  }
  thead {
    display: table-header-group;
  }
  img,
  tr {
    page-break-inside: avoid;
  }
  img {
    max-width: 100% !important;
  }
  @page {
    margin: 2cm 0.5cm;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  blockquote,
  pre {
    border: 0.1rem solid #f0f1f2;
    page-break-inside: avoid;
  }
  .content,
  .content-sidebar {
    width: 100%;
  }
  button,
  input,
  select,
  textarea,
  .breadcrumb,
  .comment-edit-link,
  .comment-form,
  .comment-list .reply a,
  .comment-reply-title,
  .edit-link,
  .entry-comments-link,
  .entry-footer,
  .genesis-box,
  .header-widget-area,
  .hidden-print,
  .home-top,
  .nav-primary,
  .nav-secondary,
  .post-edit-link,
  .sidebar {
    display: none !important;
  }
  .title-area {
    text-align: center;
    width: 100%;
  }
  .site-title > a {
    margin: 0;
    text-decoration: none;
    text-indent: 0;
  }
  .site-inner {
    padding-top: 0;
    position: relative;
    top: -10rem;
  }
  .author-box {
    margin-bottom: 0;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    orphans: 3;
    page-break-after: avoid;
    page-break-inside: avoid;
    widows: 3;
  }
  img {
    page-break-after: avoid;
    page-break-inside: avoid;
  }
  blockquote,
  pre,
  table {
    page-break-inside: avoid;
  }
  dl,
  ol,
  ul {
    page-break-before: avoid;
  }
}
